@import url(https://fonts.googleapis.com/css2?family=Jost:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost:ital@1&family=Langar&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*{
    padding: 0;
    margin: 0;
    font-family: 'Langer','Jost', sans-serif;
}

body{
    background: #f3f2ef;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    height:100vh;
    width: 100vw;
    font-family: 'Langer','Jost', sans-serif;
}

html{
    font-size: 100%;
    height: 100%;
    background: #eee;
}

.box{
    width: 25vw;
    height: 60vh;
    border-radius: 0.5rem;
    box-shadow: 0 0.2rem 3rem rgba(0, 0, 0, 0.2);
    background-image: url(/static/media/weather.9f379d85.jpg);
    background-size: cover;
    position: relative;
    overflow: hidden;
    transform: translate3d(0,0,0);
    min-width: 20rem;
    min-height: 35rem; 
}
.err{
    margin-top: 65%;
    font-size: 1.5rem;
    color: white;
}
.wave {
    opacity: 0.3;
    position: absolute;
    top:120%;
    left: 50%;
    background: white;
    width: 50rem;
    height: 50rem;
    margin-left: -25rem;
    margin-top: -20rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    -webkit-animation: drift 3000ms infinite linear;
            animation: drift 3000ms infinite linear;
    z-index: 1;
}

.wave-three{
    -webkit-animation: drift 5000ms infinite linear;
            animation: drift 5000ms infinite linear;
    z-index: 2 !important;
    opacity: 0.2;
}
.wave-two{
    -webkit-animation: drift 7000ms infinite linear;
            animation: drift 7000ms infinite linear;
    z-index: 3 !important;
    opacity: 0.1;
}

.box::after{
    content: "";
    display: block;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 11;
    transform: translate3d(0,0,0);
}

@-webkit-keyframes drift{
   from{
        transform: rotate(0deg);
    } 
    from{
        transform: rotate(360deg);
    }
}

@keyframes drift{
   from{
        transform: rotate(0deg);
    } 
    from{
        transform: rotate(360deg);
    }
}


.info{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 60vh;
    z-index: 4;
    color: rgba(0, 0, 0, 1); 
    margin-top: 65%;
}

.location{
    margin-top: -5.5rem;
    text-align: center;
}

.inputData {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 10%;
}

.inputField{
    margin-top: 20px;
    border: none;
    color: white;
    background: transparent;
    border-bottom: 0.5px solid white;
    width: 60%;
    text-align: center;
    font-size: 1rem;
}

.temp h1{
    font-size: 3rem;
    text-transform: capitalize ;
}
#weathercon{
    height: 45%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 3rem;
}

#weathercon .fas{
    font-size: 6rem;
    -webkit-animation: fas-anime 3s linear infinite alternate;
            animation: fas-anime 3s linear infinite alternate;
}

@-webkit-keyframes fas-anime{
    from{
        transform: scale(1.1)
    }
    to{
        transform: scale(1.2);
    }
}

@keyframes fas-anime{
    from{
        transform: scale(1.1)
    }
    to{
        transform: scale(1.2);
    }
}



